.wrapper {
	position: relative;
	background: $backgroundApp;
	width: $widthApp;
	max-width: $widthApp;
	height: $heightApp;
	max-height: $heightApp;
	display: $display;
	grid-template-rows: $heightHeader 1fr;
	@media (max-height: 600px) {
		grid-template-rows: $heightHeader*1.4 1fr;
		overflow-x: hidden; }
	@media (max-height: 450px) {
		grid-template-rows: $heightHeader*1.5 1fr; }
	@media (max-height: 400px) {
		grid-template-rows: $heightHeader*1.8 1fr; }

	.modal__window__container {
		position: absolute;
		background: #000000;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		opacity: 0.8;
		z-index: 10000;

		.modal__window__close_text {
			position: absolute;
			color: white;
			top: 5vw;
			right: 20vw;
			left: 35vw;
			@media (max-width: 2560px) {
				text-align: center; }
			@media (max-width: 1920px) {
				position: relative;
				right: 0;
				left: 0; } }

		.modal__window__container__info {
			position: absolute;
			width: 50vw;
			height: 50vh;
			top: 15vw;
			right: 20vw;
			z-index: 20000;
			background: $backgroundApp;
			overflow: auto;
			padding-left: 5vw;
			@media (max-width: 1920px) {
				left: 15vw;
				width: 70vw;
				height: 60vh;
				padding-left: 1vw; }
			@media (max-width: 1024px) {
				left: 10vw;
				width: 80vw; }
			@media (max-width: 800px) {
				height: 70vh; } } }

	.header {
		position: relative;
		min-height: $minHeightHeader;
		display: $display;
		grid-template-columns: $widthListRequests 1fr;
		gap: $gapHeader;
		@media (max-width: 1024px) {
			gap: $gapHeader - 20vw;
			grid-template-columns: $widthListRequests * 1.3 1fr; }
		@media (max-width: 814px) {
			gap: $gapHeader - 30vw;
			grid-template-columns: $widthListRequests * 1.5 1fr; }
		@media (max-width: 560px) {
			gap: 0;
			grid-template-columns: $widthListRequests * 2 1fr; }

		.header__request__container__search_icon {
			@extend %icon;
			margin-left: $widthListRequests / 10;
			@media (max-width: 2560px) {
				height: $heightHeader / 2;
				width: $heightHeader / 2; }
			@media (max-width: 560px) {
				display: none; }
			&:hover {
				color: rgba(0, 7, 117, 0.95); } }

		.header__request__container__input {
			background: $backgroundApp;
			height: $heightHeader / 3;
			padding-left: $widthListRequests / 15;
			width: $widthListRequests * 3 / 4;
			border-radius: 10vw;
			@media (max-width: 2560px) {
				height: $heightHeader / 1.5; }
			@media (max-width: 1024px) {
				width: $widthListRequests; }
			@media (max-width: 560px) {
				width: 100%; } }

		.header__version__container {
			display: $display;
			grid-template-columns: repeat(2, 1fr);
			justify-items: center;
			gap: $gapHeader / 10;

			.header__version__container__view {
				@extend %icon; } } }

	.main {
		display: $display;
		grid-template-columns: $widthListRequests 1fr;
		margin: 0!important;
		overflow-y: hidden;
		@media (max-width: 560px) {
			grid-template-columns: 1fr;
			grid-template-rows: repeat(2, 1fr); }

		.list_request__container {
			border-right: 1px solid #1d56fd;
			min-width: $minWidthListRequest;
			max-height: $heightApp - $heightHeader;
			overflow-y: auto;
			overflow-x: hidden;
			@media (max-width: 1024px) {
				min-width: $minWidthListRequest - 150px; }
			@media (max-width: 560px) {
				border-right: none;
				border-bottom: 1px solid #1d56fd; }

			.card_request__container {
				@extend %blocksInListRequests;
				@media (max-width: 1024px) {
					min-width: $minWidthListRequest - 150px; }
				@media (max-width: 560px) {
					min-width: 100%; }
				@media (max-height: 600px) {
					height: (($heightApp - $heightHeader) / 6) - $heightDividerInListRequests; }

				button {
					@extend %blocksInListRequests;
					display: $display;
					@media (max-width: 1024px) {
						min-width: $minWidthListRequest - 150px; }
					@media (max-width: 560px) {
						min-width: 100%; } } } }

		.response__info__container {
			max-height: $heightApp - $heightHeader;
			padding: 1vw;
			overflow-y: auto;

			li {
				display: $display;
				justify-content: center; }

			h4 {
				@extend %loading; }

			.response__info__spinner {
				display: $display!important;
				justify-self: center; } }

		.response__info__spinner {
			display: $display;
			margin: 10vw auto; }

		.response__info__not_found {
			@extend %loading; }


		.response__info__error {
			@extend %loading; } } }
